*:focus {
    outline: none;
}
html {
    /* 1rem = 10px */
    font-size: 62.5%;
    scroll-behavior: smooth;
    width:100%;
    overflow-x:hidden;
  }
  body {
    font-size: 1.6rem;
    width:100%;
    overflow-x:hidden;
  }
  
@include main; 

.container {
    margin: 0 auto;
    max-width: 1140px;

    @media only screen and(max-width: 1200px) {
        max-width: 1000px;
    }
    @media only screen and(max-width: 1100px) {
        max-width: 800px;
    }
    @media only screen and(max-width: 960px) {
        max-width: 500px;
    }
    @media only screen and(max-width: 550px) {
        max-width: 400px;
        padding: 0 30px;
    }
}

